	module.exports = {"data":{"wordpressPage":{"title":"Truck","content":"<header class=\"post-header\"></header>\n<section class=\"post-content\">Onze rollende keuken is een pipowagen/schaftkeet die is omgebouwd tot een volledig uitgeruste professionele keuken met friteuses, gasfornuis, koelwerkbank en een oven. Afhankelijk van het menu kunnen we 250 porties per uur verwerken en het is mogelijk om de capaciteit voor grotere festivals uit te breiden om de vraag aan te kunnen.</p>\n<h3 id=\"technischespecificaties\">Technische specificaties</h3>\n<h3>De maten van de kar zijn:</h3>\n<ul>\n<li>Hoogte 3.10(incl. afzuigpijp)</li>\n<li>Lengte 5.70(incl. de dissel &#8220;trekhaak&#8221;)</li>\n<li>Breedte 2.10</li>\n</ul>\n<h3 id=\"electra\">Electra</h3>\n<p>Aansluiting van 230V 16 amp. benodigd voor afzuiginstallatie, licht, koeling en muziek natuurlijk!</p>\n<h3 id=\"water\">Water</h3>\n<p>Eventueel een gardena aansluiting voor stromend water.<br />\nHandwash jerrycan aanwezig, dus stromend water/afvoer is geen must.</p>\n<h3 id=\"gas\">Gas</h3>\n<p>Wij koken op propaangas, vier gasflessen (a 10,5 kg) staan in de daarvoor bestemde en ontworpen gasopslag (op de dissel) die voorzien is van een goede en erkende gasaansluiting met bijbehorende gasklep en gekeurde slangen.</p>\n<h3 id=\"brandblusser\">Brandblusser</h3>\n<p>De verplichte brandblusser is aanwezig.</p>\n<h3 id=\"certificaten\">Certificaten</h3>\n<p>HACCP certificaat is behaald door de chef.<br />\nWe werken volgens de HACCP richtlijnen met codering en het bijhouden van een logboek hiervoor.</p>\n</section>\n","slug":"truck","id":"66dd199f-795b-5270-b6e7-74bfab3cf55d","date":"February 06, 2018"},"site":{"id":"Site","siteMetadata":{"title":"Pass The Peas"}}},"pathContext":{"id":"66dd199f-795b-5270-b6e7-74bfab3cf55d"}}

